
.TilesWrapper{
    display: grid;
    grid-template-columns: repeat(4,0.15fr);
    margin-left: 5%;
    margin-top: 5%;
    grid-column-gap: 5%;
    grid-row-gap: 5%;
    justify-content: center;

}
.Card{

    width: 80px;
    height: 70px;

}
.CardBackSide{
    border-style: solid;
    border-width: 2px;
    border-color: white;
    border-radius: 3px;
    width: 60px;
    height: 90px;

}
.GameOverText{
margin-top: 10%;
}
.GameOverButtonsPose{
    margin-top: 5%;
}

@media (min-width: 576px) {
    .Card{
        width: 100px;
        height: 110px;
    }
    .CardBackSide{
        border-style: solid;
        border-width: 3px;
        border-color: white;
        border-radius: 4px;
        width: 70px;
        height: 100px;
    }
}


@media (min-width: 768px) {
    .Card{
        width: 120px;
        height: 130px;
    }
    .CardBackSide{
       border-style: solid;
        border-width: 4px;
        border-color: white;
        border-radius: 7px;
        width: 90px;
        height: 130px;
    }
}

@media (min-width: 992px) {

}

@media (min-width: 1200px) {
    .Card{
        width: 120px;
        height: 130px;
    }
    .CardBackSide{
        border-style: solid;
        border-width: 5px;
        border-color: white;
        border-radius: 8px;
        width: 100px;
        height: 135px;

    }
}

.InfoPopper{
    margin-left: 45%;
    margin-top: 5%;
}